#root{
    padding: 50px 0 50px 150px;
    background: pink;
}
.bor {
    width: 80%;
    text-align: center;
    box-shadow: 5px 5px 5px 5px;
}

.stu {
    width: 200px;
    padding: 30px;
    margin: 30px;
    font-size: 25px;
    text-align: center;
    border: solid 1px gray;
    box-shadow: 0px 5px 2px gray;
    display: inline-block;
    background: red;
}

.close {
    padding: 10px 15px;
    font-weight: bold;
}

.close:hover {
    padding: 15px 20px;
    color: rgb(18, 139, 237);
}